

*{
	box-sizing:	border-box;
}
body{
	width:1000px;
	margin:0 auto; 
	padding:0;
	background:	steelblue;
}
.form-container{
	width: 800px;
	height:500px;
	margin: 30px auto 25px;
	background-image: url("images/img2.jpg");
	background-size	:cover;
	overflow: hidden;
     filter: blur(0.2px); 
	

}
.login-form{
	width: 450px;
	height:400px;
	margin: 50px auto 25px;
	
	padding:50px;
	background :rgba(0,0,0,0.5);
}
.login-form input{
	width:70%;
	padding-left:20px;
	margin-bottom: 30px;	
	border : none;
	
}
.login-form input{
	width:70%;
	height:35px;
}

.login-form p{
	margin:0;
	padding:0;
}
.login-form	::placeholder{
	text-transform: capitalize;
	font-family: 'Titillium Web', sans-serif;
}

.login-form button {
	width:70%;
	height: 40px;
	font-family: 'Titillium Web', sans-serif;
	text-align: center;
	text-transform: uppercase;
	border: 2px solid #fff;
	background: steelblue;
	color: #fff;
	cursor: pointer;
}

.login-form button:hover{
	background:#000;
	color:steelblue;
	border-radius: 25px;
}





