#tbl {
    font-family:  sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  #tbl td, #tbl th {
    border: 1px solid #ddd;
     text-align: center;
    padding: 8px;
  }
  
  
  
  #tbl th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: blue;
    color: white;
  }